<template>
	<div class="register-content">
		<form action="">
			<register-item :login-info="loginInfo"  @textBlur="textBlur"  :info="comInfos"/>
			<!--上面封装了表单的子组件-->
			<register-item :login-info="loginInfo"  @textBlur="userBlur"  :info="usernameInfos"/>
			<!--上面封装了表单的子组件-->
			<register-item :login-info="loginInfo"  @textBlur="passBlur"  :info="passwordInfos"/>
			<!--上面封装了表单的子组件-->
			<register-item :login-info="loginInfo"  @textBlur="emailBlur"  :info="emailInfos"/>
			<!--上面封装了表单的子组件-->
			<register-item :login-info="loginInfo"  @textBlur="teleBlur"  :info="teleInfos"/>
			<!--上面封装了表单的子组件-->

			<button class="seed-icon" type='button' @click="registerClick">注册</button>
		</form>
		<div class="point">提示：
			<ul>
				<li>1.以上内容为必填信息</li>
				<li>2.提示2</li>
				<li>3.提示3</li>
			</ul>
		</div>
	</div>
</template>

<script>
import RegisterItem from './RegisterItem'

export default {
  name: 'RegisterContent',
  components:{
	RegisterItem
  },
  props:{
	loginInfo:{
		type:Object,
		default(){
			return{}
		}
	}
  },
  data(){
	return{
		//可以包装一个对象传到里面去
		usernameInfos:{
			name:'username',
			checkText:'',
			isPass:false,
			inputtext:'用户名',
			placeholderText:'请输入用户名',
		},
		passwordInfos:{
			name:'password',
			text:'',
			isPass:false,
			inputtext:'密码',
			checkText:'',
			placeholderText:'请输入密码',
			
		},
		emailInfos:{
			name:'email',
			checkText:'',
			isPass:false,
			inputtext:'邮箱',
			placeholderText:'请输入邮箱',
			
		},
		comInfos:{
			name:'company',
			checkText:'',
			isPass:false,
			inputtext:'公司名',
			placeholderText:'请输入公司名',
		},
		teleInfos:{
			name:'telephone',
			checkText:'',
			isPass:false,
			inputtext:'手机号',
			placeholderText:'请输入手机号',
			
		},
	}
  },
  methods:{
	registerClick(){
		this.$emit('registerClick')
	},
	visitorClick(){
		this.$emit('visitorClick')
	},
	//用户失去焦点
	userBlur(info){
		//这里需要发送到后台验证一下用户是否已经存在
		if(info.length<3 || info.length>8){
			this.usernameInfos.isPass=false
			this.usernameInfos.checkText='用户名长度应为3~8个字符';
		}else{
			this.usernameInfos.isPass=true
			this.usernameInfos.checkText="恭喜,该用户名可以使用"
		}
	},
	//密码失去焦点
	passBlur(info){
		if(info.length<6 || info.length>30){
			this.passwordInfos.isPass=false
			this.passwordInfos.checkText='密码长度应为6~30个字符';
		}else{
			this.passwordInfos.isPass=true
			this.passwordInfos.checkText="恭喜,密码可以使用"
		}
	},
	//邮箱失去焦点
	emailBlur(info){
		let myReg = /^[a-zA-Z0-9_-]+@([a-zA-Z0-9]+\.)+(com|cn|net|org)$/;
		if(!myReg.test(info)){
			this.emailInfos.isPass=false
			this.emailInfos.checkText="邮箱格式必须为：xxx@xx.com"
			return false
		}else{
			this.emailInfos.isPass=true
			this.emailInfos.checkText="恭喜,邮箱可以使用"
		}
	},
	//电话失去焦点
	teleBlur(info){
		if(!(/^1[3456789]\d{9}$/.test(info))){
			this.teleInfos.isPass=false
			this.teleInfos.checkText="手机号码格式不正确"
			return false
		}else{
			this.teleInfos.isPass=true
			this.teleInfos.checkText="恭喜,手机号可以使用"
		}
	},
	//失去焦点
	textBlur(info){
		//这里需要发送到后台验证一下用户是否已经存在
		if(info.length<3 || info.length>8){
			this.comInfos.isPass=false
			this.comInfos.checkText='公司名称长度3~8';
		}else{
			this.comInfos.isPass=true
			this.comInfos.checkText="合法，可以使用"
		}
	}
  }
}
</script>


