<template>
	<div id="register">
		<!--顶部菜单-->
		<nav-bar class="register-nav-bar">
			<div slot="left" class="icon-left" @click="backClick">
				<img src="~assets/images/common/prev2.svg">
			</div>
			<div slot="center">
				注册
			</div>
		</nav-bar>
		<register-content @registerClick="registerClick" :login-info="registerInfo" ref="resform"/>
	</div>
</template>

<script>


import NavBar from 'components/common/navbar/NavBar'
import RegisterContent from './childComps/RegisterContent'

import {register} from 'network/login'

export default {
  name: 'Register',
  components:{
	NavBar,
	RegisterContent,
  },
   data(){
	return{
		registerInfo:{
			username:'',
			password:'',
			telephone:'',
			email:'',
			company:'',
			isCheckPass:false			
		},
		message:'',
	}
  },
  //https://blog.csdn.net/weixin_42406046/article/details/80624932
  methods:{
	//注册
	registerClick(){
		//去除空格
		let username=this.registerInfo['username'].replace(/\s*/g,"")
		let password=this.registerInfo['password'].replace(/\s*/g,"")
		let email=this.registerInfo['email'].replace(/\s*/g,"")
		let telephone=this.registerInfo['telephone'].replace(/\s*/g,"")
		//判断几个东西不能为空
		
		if(username==""){
			this.$toast.show('用户名不能为空',1500)
			return false
		}else if(password==""){
			this.$toast.show('密码不能为空',1500)
			return false
		}else if(email==""){
			this.$toast.show('邮箱不能为空',1500)
			return false
		}else if(telephone==""){
			this.$toast.show('手机号不能为空',1500)
			return false
		}
		
		//下面验证长度
		if(username.length<2 || username.length>8){
			this.$toast.show('用户名长度：2 - 8位',1500)
			return false
		}
		//下面密码长度
		if(password.length<6 || password.length>18){
			this.$toast.show('密码：6 - 18位',1500)
			return false
		}
		//验证邮箱的
		let myReg = /^[a-zA-Z0-9_-]+@([a-zA-Z0-9]+\.)+(com|cn|net|org)$/;
		//邮箱格式
		if(!myReg.test(email)){
			this.$toast.show("邮箱格式不正确",1500)
			return  false
		}
		//验证手机号码
		if(!(/^1[3456789]\d{9}$/.test(telephone))){
			//后台没有记录到手机号
			this.$toast.show("手机号码格式有误",1500)
			return  false
		}
		
		//以上都通过
		//发送到后台
		register(this.registerInfo).then(res=>{
				if(res==0){
					this.$toast.show('注册成功,请登录',1500)
					//跳转登录页面
					this.$router.push('/login').catch(res=>{res})
				}else{
					this.$toast.show(res,1500)
				}
		})
	},
	//返回按钮
	backClick(){
		this.$router.go(-1)
	},
	//数据验证和限制-(这里最好写在工具类里面)
	checkInfo(){
		//1.限制长度-2.不能为空
	}
  },
  created(){
	document.title="登录"
  }
}
</script>

